.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Responsive columns */
  gap: 20px;
  justify-content: center; /* Center the cards in the grid */
  padding: 20px; /* Add padding around the grid */
  margin-left: 500px; /* Shift grid to the right (adjust to fit your sidebar) */
}

.project-card {
  background-color: white;
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); /* Soft shadow for the card */
  padding: 20px;
  max-width: 450px; /* Limit the card width */
  height: auto; /* Let height adjust dynamically */
  text-align: center;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
}

.project-card:hover {
  transform: scale(1.10); /* Enlarge the card slightly on hover */
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2); /* Add more shadow for hover */
}

.project-card img {
  width: 100%; /* Image spans the width of the card */
  height: auto; /* Maintain aspect ratio */
  max-height: 295px; /* Limit image height for consistency */
  border-radius: 8px; /* Rounded corners for the image */
  margin-bottom: 10px; /* Space below the image */
  object-fit: cover; /* Ensures the image fills its container neatly */
}

.project-card:hover img {
  transform: scale(1.02); /* Slightly enlarge the image on hover */
}

.project-card-content {
  text-align: center; /* Center-align the text content */
}

.project-card-content h3 {
  font-size: 1.5rem; /* Title font size */
  margin: 10px 0; /* Space around the title */
  color: #333; /* Darker text color */
  word-wrap: break-word; /* Ensure long titles wrap */
  overflow: hidden; /* Prevent content overflow */
}

.project-card-content p {
  font-size: 1rem; /* Description font size */
  color: #555; /* Softer gray color for text */
  margin-bottom: 10px; /* Space below the description */
  word-wrap: break-word; /* Ensure text wraps nicely */
}
