/* Styles for About Section */
.about-section {
  padding: 30px 15px; /* Inner spacing */
  background-color: #f9f9f9; /* Light background */
  width: 70%; /* Reduce the overall width */
  max-width: 800px; /* Constrain the max size */
  margin: 50px auto; /* Center-align */
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow */
}

.about-content {
  display: flex;
  flex-direction: column;
  gap: 30px; /* Space between sections */
}

.about-content h2 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.about-item h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  font-weight: bold;
  color: #555;
}

.about-item p {
  font-size: 1rem;
  color: #666;
  line-height: 1.6; /* Improve readability */
}
